<template>
    <div style="margin-left:0;">
        <!-- <br>
        <h1>首页</h1> -->
        <br>
        <!-- <InArticleAdsense
            data-ad-client="ca-pub-5072970286349933"
            data-ad-slot="6330202821">
        </InArticleAdsense> -->
        <div v-show="existZip">
            <template v-if="downloadUrl">
                <h3>Download pictures!</h3>
                <a :href="downloadUrl" :download="getZipName()" target="_blank">
                    <input class="greenbtn" type="button" value="下载资源" />
                </a>
                <h3>Resubmit your picture!</h3>
                <button class="greenbtn" @click.prevent="setExistZip(false)" >重新提交</button>
            </template>
            <template v-else>
                <h1>资源上传中...</h1>
            </template>
        </div>
        <form v-show="!existZip" method="post" action="" report-submit @submit.prevent="submitFormData($event)">
            <!-- <input type="text" name="url" class="form-control" placeholder="请输入图片上传地址" value="http://123.56.84.189:8087/image/upload" required>
            <input type="text" name="appname" class="form-control" placeholder="appname" value="test" required>
            <input type="text" name="appKey" class="form-control" placeholder="appKey" value="ad%4a*a&ada@#ada" required> -->
            <h3>Choose your picture!</h3>
            <img src="" id="preview" width="120">
            <label class="file" for="xFile">选择图片</label>
            <input type="file" id="xFile" name="upload" accept="image/png, image/jpg, image/jpeg" 
            style="position:absolute;clip:rect(0 0 0 0);" @change="changeFile($event)"/>
            <!-- 单选框 -->
            <h3>Choose the type!</h3>
            <div class="choice">
                <input class="radio" v-model="opType" type="radio" name="fruit" value="AppIcon" checked="checked">
                <span style="color:#1E88C7">应用图标</span>
                <!-- https://www.sunofbeach.net/a/1236159948359053312 -->
                <input class="radio" v-model="opType" type="radio" name="fruit" value="Launching">
                <span style="color:#1E88C7">启动图</span>
            </div>
            <!-- 提交 -->
            <br/>
            <!-- <p><input class="greenbtn" type="submit" @click.prevent=""></p> -->
            <button class="file" formType="submit">提交</button>
        </form>
    </div>
</template>

<script>
import { mapState } from "vuex"
import store from "@/store/index.js"

export default {
    data(){
        return {
            opType: "AppIcon",
            fileBase64: "",
        }
    },
    //计算属性
    computed:{
        //组件中使用仓库的数据
        ...mapState("home", ["existZip","downloadUrl"],),
    },
    methods:{
        setExistZip(param) {
            store.commit('home/setExistZip', param);
        },
        setDownloadUrl(param = "") {
            store.commit("home/setDownloadUrl", param);
        },
        changeFile(e) {
            var file = e.target.files[0];
            let fileObjectUrl = window.URL.createObjectURL(file);
            // var fileBase64 = new Promise((resolve, reject) => {
            //     const reader = new FileReader()
            //     reader.readAsDataURL(file)
            //     reader.onload = function () {  // 图片转base64完成后返回reader对象
            //         resolve(reader)
            //     }
            //     reader.onerror = reject
            // })
            this.fileBase64 = file
            // console.log("changeFile", e, file, fileObjectUrl, fileBase64, this.fileBase64);
            document.getElementById('preview').src=fileObjectUrl;
        },
        getNowFormatDate() {
            var date = new Date();
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            var currentDate = date.getFullYear() + "-" + month + "-" + strDate
                    + "_" + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();
            return currentDate;
        },
        getZipName() {
            return this.opType + this.getNowFormatDate() + ".zip"
        },
        submitFormData(e){
            if (!this.fileBase64) {
                return ;
            }
            this.setExistZip(true);
            this.setDownloadUrl();
            const formData = new FormData(e.target);
            store.dispatch("home/fetchDatas", formData);
            const value = {};
            for(let pair of formData.entries()) {
                value[pair[0]] = pair[1];
            }
            console.log("submitFormData", value)
        },
    }
}
</script>

