    padding: 4px 12px;
    overflow: hidden;
    color: #1E88C7;
    text-decoration: none;
    text-indent: 0;
    line-height: 20px;
    font-size: 20px;
    font-weight: bold;
}
.file input {
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
}
.file:hover {
    background: #AADFFD;
    border-color: #78C3F3;
    color: #004974;
    text-decoration: none;
}
.choice{
    position: relative;
}
.choice .radio{
    position: relative;
    display: inline-block;
    font-weight: 400;
    color: #0c4757;
    padding-left: 25px;
    cursor: pointer;
}
.choice .radio input{
    position: absolute;
    left: -9999px;
}
.choice .radio i{
    display: block;
    position: absolute;
    top: 6px;
    left: 0;
    width: 15px;
    height: 15px;
    outline: 0;
    border: 1px solid #47a8c0;
    background: #9cd9e8;
    border-radius: 50%;
    transition: border-color;
    -webkit-transition: border-color .3s;
}
.choice .radio input:checked+i{
    border-color: #0c4757;
}
.choice .radio input+i:after{
    position: absolute;
    content: '';
    top: 3px;
    left: 3px;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #47a8c0;
    opacity: 0;
    transition: opacity .1s;
    -webkit-transition: opacity .1s;
}
.choice .radio input:checked+i:after{
    opacity: 1;
}
</style>